import { Component, Input, ContentChild } from '@angular/core';
import { NgControl, FormControl, FormControlName } from '@angular/forms';
import { BaseComponent } from '../base-component';

@Component({
    moduleId: module.id,
    selector: 'azteca-form-field',
    templateUrl:'./form-field.component.html'
})
export class FormFieldComponent {
    @Input() label: string;    

    @ContentChild(FormControlName) state: FormControlName; 

    constructor() {
        
    }

    ngAfterViewInit() {
        if (this.state && this.state.valueAccessor && this.state.valueAccessor instanceof BaseComponent) {
            if (!this.state.valueAccessor.name) this.state.valueAccessor.name = this.state.name;
        }
    }

}